<template>
  <div style="margin-top:15px;font-size:0.8rem;position:fixed;top:50px;bottom:0px;left:0px;right:0px;overflow-y:scroll;">
    <!-- 办理入住---长租 -->
    <div
      v-if="orderType == 3 && back_num != 9 || orderType == 4 && back_num != 9"
      id="bigbox"
    >
      <mt-field
        label="订单状态"
        v-model="month_payment[order_status]"
        readonly
      ></mt-field>
      <mt-field
        label="选择房间"
        v-if="order_status==0"
        v-model="long_num"
        placeholder="租客付款后才能办理入住"
        readonly
      ></mt-field>
      <!-- <van-field
        v-else
        label="选择房间"
        @click="btn_choose"
        :value="long_num"
      /> -->
      <mt-field
        label="选择房间"
        v-else
        v-model="long_num"
        readonly
        @click.native="btn_choose"
        class="box_1"
      ></mt-field>
      <p>租赁人信息</p>
      <mt-field
        label="姓名"
        v-model="userName"
        placeholder="请输入完整姓名"
      ></mt-field>
      <mt-field
        label="证件号"
        type="text"
        :attr="{ maxlength: 18 }"
        v-model="IDcard"
        placeholder="请输入证件号"
      ></mt-field>
      <mt-field
        label="手机号"
        type="tel"
        :attr="{ maxlength: 11 }"
        v-model="phone"
        placeholder="请输入手机号"
      ></mt-field>
      <p>租赁信息</p>
      <mt-field
        label="起租日期"
        v-model="star_time"
        readonly
      ></mt-field>
      <mt-field
        label="到租日期"
        v-if="orderType == 4"
        v-model="sort_endTime"
        readonly
      ></mt-field>
      <mt-field
        label="首期入住"
        v-if="orderType == 4"
        v-model="sort_diffDay"
        readonly
      ></mt-field>
      <mt-field
        label="租赁期限"
        v-if="orderType == 3"
        v-model="rent_time"
        readonly
      ></mt-field>
      <mt-field
        label="支付方式"
        v-if="orderType == 3"
        v-model="payments[pay_num]"
        readonly
      ></mt-field>
      <mt-field
        label="每月租金"
        v-if="orderType == 3"
        v-model="roomRealAmount"
        readonly
      ></mt-field>
      <mt-field
        label="每日租金"
        v-if="orderType == 4"
        v-model="roomRealAmount"
        readonly
      ></mt-field>
      <mt-field
        label="押金"
        v-model="deposit"
        readonly
      ></mt-field>
      <mt-button
        type="primary"
        @click="bthDeal"
        class="next statu_0"
        disabled
        v-if="order_status==0"
      >下一步</mt-button>
      <mt-button
        type="primary"
        @click="bthDeal"
        class="next"
        v-else
      >{{lifeCost!=0?'下一步':'完成办理'}}</mt-button>
      <!-- </div> -->
    </div>
    <!-- 办理入住---日租、小时房 -->
    <!-- back_num != 9  -->
    <div v-if="orderType == 1 && back_num != 9 && !item.roomSn || orderType == 2 && back_num != 9 && !item.roomSn">
      <div class="roomTitle">完善身份信息</div>
      <mt-field
        label="姓名"
        v-model="userName"
        placeholder="请输入完整姓名"
      ></mt-field>
      <mt-field
        label="证件类型"
        v-model="type"
        readonly
      ></mt-field>
      <mt-field
        label="证件号"
        v-model="IDcard"
        type="text"
        :attr="{ maxlength: 18 }"
        placeholder="请输入证件号"
      ></mt-field>
      <mt-field
        label="房间号"
        v-model="long_num"
        readonly
        @focus.native.capture="btn_choose"
      ></mt-field>
      <div>
        <mt-field
          label="手机号"
          v-model="phone"
          placeholder="请输入手机号"
          :attr="{ maxlength: 11 }"
          type="tel"
        ></mt-field>
        <mt-field
          label="车牌号"
          v-if="item.hotel.isPark"
          v-model="carNum"
          placeholder="请输入车牌号"
        ></mt-field>
      </div>
      <div v-if="item.order.payTimeType == 2">
        <div class="roomTitle">付款信息</div>
        <div v-if="item.order.payTimeType == 2">
          <mt-field
            label="到店付款"
            v-model="roomPriceInput"
            readonly
          >
            <span
              style="padding-right:1rem;color:#02BC6C;"
              v-if="pricePayment"
            >{{ month_payment[pricePayment] }}</span>
            <span
              v-if="!pricePayment"
              @click="bthShowPicker({type:'roomPrice'})"
              :style="!pricePayment?{'padding-right':'1rem','color':'red'}:{'padding-right':'1rem','color':'#02BC6C'}"
            >
              {{ !pricePayment?'未支付': roomPricePaymentName }}&emsp;></span>
          </mt-field>
          <div v-if="roomPriceArr.length">
            <div
              v-for="(room,i) in roomPriceArr"
              :key='i'
            >
              <mt-field
                :label="room.time | dataFilter('MM月DD日')"
                v-model="room.price"
                style="min-height:30px;color:#B2B2B2;"
                readonly
              >
                <span style="padding-right:7rem;color:#888;">{{'*' + item.order.roomNum }} 间</span>
              </mt-field>
            </div>
          </div>
        </div>
        <div
          v-if="item.deposit > 0"
          :style="{'margin-top':item.order.payTimeType == 2?'10px':''}"
        >
          <mt-field
            label="押金"
            v-model="depositInput"
            readonly
          >
            <span
              style="padding-right:1rem;color:#02BC6C;"
              v-if="depositPayment"
            >{{ month_payment[depositPayment] }}</span>
            <span
              v-else
              @click="bthShowPicker({type:'deposit'})"
              :style="!depositPaymentName?{'padding-right':'1rem','color':'red'}:{'padding-right':'1rem','color':'#02BC6C'}"
            >{{ !depositPaymentName?'未支付':depositPaymentName }}&emsp;>
            </span>
          </mt-field>
        </div>
      </div>
      <van-popup
        v-model="showPicker"
        position="bottom"
      >
        <van-picker
          show-toolbar
          :columns="columns"
          @confirm="onConfirm"
          @cancel="showPicker = false"
        />
      </van-popup>
      <!-- 长租提醒 -->
      <div class="long_tips">
        <p>提示：</p>
        <p>1、酒店帮助顾客办理入住将不会通过平台收取押金。</p>
        <p>2、顾客如果需要办理续住，可通过上面的手机号登录小程序来办理续住。</p>
        <p>3、请确保预留手机号与客人微信或支付宝绑定手机号相同。</p>
        <p>4、顾客若无法使用密码开门，请客人扫门上二维码，在我的订单上查询、更改密码，或者使用离线密码开门！</p>
      </div>
      <mt-button
        type="primary"
        @click="bthDeal"
        style="margin-top:20px;margin-bottom:15px;width:90%;background-color:#f60;"
      >办理入住</mt-button>
    </div>
    <mt-popup
      v-model="show_num "
      v-if="numList.length != 0"
      position="bottom"
      class="popup"
    >
      <div
        v-for="(item,i) in numList"
        :key="i"
        class="num_box"
        @click="choosed(item)"
      >{{ item.roomSn }}</div>
    </mt-popup>
    <!-- 扣款押金 -->
    <div v-if="item.status == 4 && this.$route.query.num != 9">
      <div style="text-align: left;padding:0 0 10px 20px;">订单信息</div>
      <mt-field
        label="姓名"
        v-model="realName"
        readonly
        style="text-align:left;padding-left:20px;color:#000;"
      ></mt-field>
      <mt-field
        label="房型"
        v-model="scaleName"
        readonly
        style="text-align:left;padding-left:20px;color:#000;padding-right:10px;"
      >
        <span>共1间</span>
      </mt-field>
      <mt-field
        label="订单时间"
        readonly
        v-model="time"
        style="color:#000;padding-left:20px;text-align:left;padding-right:10px;"
      >
        &emsp;&emsp;共
        <span>{{ item.order.diffDay }}</span>
        {{item.orderType==2?'小时':'晚'}}
      </mt-field>
      <div style="margin-top:20px;">
        <mt-field
          label="扣款金额"
          type="number"
          step="1"
          min="0"
          @keyup="this.value= this.value.match(/\d+(\.\d{0,2})?/) ? this.value.match(/\d+(\.\d{0,2})?/)[0] : ''"
          v-model="redeposit"
          style="color:#000;padding-left:20px;text-align:left;padding-right:10px;"
          placeholder="请输入扣款金额"
        >
          <span style="color:#f60;padding-right:10px;">最多{{ max_deposit }}元</span>
        </mt-field>
        <mt-field
          label="扣款原因"
          style="color:#000;padding-left:20px;text-align:left;padding-right:10px;"
          v-model="reason"
          placeholder="请简要描述扣款原因"
          :attr="{ maxlength: 64 }"
          type="text"
        ></mt-field>
      </div>
      <mt-button
        type="primary"
        @click="bthdeposit"
        style="margin-top:50px;margin-bottom:15px;width:90%;background-color:#f60;"
        :disabled="disabledBtn"
      >提交</mt-button>
    </div>
    <!--  房费优惠 -->
    <div v-if="item.status == 1 && back_num == 9 || item.status == 2 && back_num == 9">
      <div style="text-align: left;padding:0 0 10px 20px;">订单信息</div>
      <mt-field
        label="姓名"
        v-model="realName"
        readonly
        style="text-align:left;color:#000;"
      ></mt-field>
      <mt-field
        label="房型"
        v-model="scaleName"
        readonly
        style="text-align:left;color:#000;padding-right:10px;"
      >
        <span>共1间</span>
      </mt-field>
      <mt-field
        label="订单时间"
        v-model="time"
        readonly
        style="color:#000;text-align:left;padding-right:10px;"
      >
        共
        <span>{{ item.order.diffDay }}</span>
        {{item.orderType==2?'小时':'晚'}}
      </mt-field>
      <div style="margin-top:20px;">
        <mt-field
          label="房费优惠"
          type="number"
          style="color:#000;text-align:left;padding-right:10px;"
          v-model="back_roomPrice"
          placeholder="请输入优惠金额"
        >
          <span style="color:#f60;padding-right:10px;">最多{{ min_value }}元</span>
        </mt-field>
      </div>
      <p style="margin-top:10px;color:#9c9c9c;">退房前,优惠金额可以多次修改,以最后一次为准</p>
      <mt-button
        type="primary"
        style="margin-top:50px;margin-bottom:15px;width:90%;background-color:#f60;"
        @click="btnturnback"
      >提交</mt-button>
    </div>
  </div>
</template>
<script>
import { Toast, MessageBox } from 'mint-ui';
import { GetSystemDictDataByType } from '@/api/api_query';
import { PostBusinessOrderroomRefuseLandlord, PostBusinessOrderroomDepositCut, GetBusinessRoomscaleDiscountInfo, GetBusinessRoomFreeRoomscale, PostBusinessOrderroomRoomin } from '@/api/api_business_hotel_and_scale';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            userName: '',
            scaleName: '',
            IDcard: '',
            phone: '',
            type: '身份证',
            carNum: '',
            remark: '',
            item: '',
            time: '',
            realName: '',
            redeposit: '',
            deposit: '', // 退款押金
            reason: '',
            id: '', // 房间id
            roomPrice: '',
            back_num: '',
            back_roomPrice: '',
            back_reason: '',
            min_value: '',
            max_deposit: '',
            disabledBtn: false,
            long_num: '选择房间号 >',
            numList: [],
            show_num: false,
            star_time: '',
            sort_endTime: '',
            payments: [],
            month_payment: [],
            pay_num: '',
            roomRealAmount: '',
            rent_time: '',
            order_status: '',
            payed: '已付款',
            orderType: '',
            lifeCost: '',
            totalPrice: '',
            sort_diffDay: '',
            depositInput: null,
            roomPriceInput: null,
            depositPayment: null,
            pricePayment: null,
            roomPricePaymentName: null,
            depositPaymentName: null,
            showPicker: false,
            color_num: false,
            columns: [],
            roomPriceArr: [],
            typeInput: null
        };
    },
    created() {
        if (JSON.parse(localStorage.getItem('orderDetail')).num == 9) {
            this.item = JSON.parse(localStorage.getItem('orderDetail')).item;
            this.back_num = JSON.parse(localStorage.getItem('orderDetail')).num;
        } else {
            this.item = JSON.parse(localStorage.getItem('orderDetail'));
        }
        this.scaleName = this.item.roomScale.scaleName;
        this.otherAmount = this.item.roomScale.otherAmount;
        this.lifeCost = this.item.virtualRoomScale.lifeCost;
        this.orderType = this.item.orderType;
        this.star_time = this.item.order.startTime.substr(0, 10);
        this.end_time = this.item.order.endTime.substr(0, 10);
        this.getMonths(this.item.order.startTime, this.item.order.endTime);
        this.pay_num = this.item.order.paymentMethod;
        this.deposit = this.item.deposit;
        if (this.deposit > 0) {
            this.depositInput = '￥' + this.deposit.toFixed(2);
            this.depositPayment = this.item.order.depositPayment;
        }
        if (this.item.roomPrice > 0) {
            this.roomPriceInput = '￥' + this.item.roomPrice.toFixed(2);
            this.pricePayment = this.item.order.payment;
        }
        if (this.pricePayment > 0 || this.depositPayment > 0 || this.orderType == 3 || this.orderType == 4) {
            this.month_peymant();
        }
        if (this.item.priceRecord) {
            this.aaa = JSON.parse(this.item.priceRecord);
            let arr = [];
            for (const k in this.aaa) {
                let obj = {};
                obj.time = k;
                obj.price = '￥' + Number(this.aaa[k]).toFixed(2);
                arr.push(obj);
            }
            this.roomPriceArr = arr.sort((a, b) => {
                return Date.parse(a.time) - Date.parse(b.time);
            });
        }
        this.roomRealAmount = this.item.order.price;
        this.order_status = this.item.order.payment;
        this.totalPrice = this.item.order.totalPrice ? this.item.order.totalPrice : '0';
        if ((this.item.orderType == 1 && this.item.order.payment == 0 && this.item.order.payTimeType == 2) || (this.item.orderType == 2 && this.item.order.payment == 0 && this.item.order.payTimeType == 2)) {
            MessageBox.confirm('此订单是到付订单，顾客还未支付房费，继续办理？')
                .then(() => {})
                .catch(() => {});
        }
        if (this.item.orderType == 3 || this.item.orderType == 4) {
            if (this.order_status == 0) {
                this.long_num = '租客付款后才能办理入住';
            } else {
                if (this.item.roomSn) {
                    this.getMinValue();
                }
            }
        }
        if (this.item.orderType == 4) {
            this.sort_diffDay = this.item.order.diffDay + '天';
            const days = dayjs(this.star_time);
            this.sort_endTime = days.add(this.item.order.diffDay, 'day').format('YYYY-MM-DD');
        }
        if (this.item.orderType == 3) {
            this.peymant();
        }
        if (this.item.sysUser) {
            this.userName = this.item.sysUser.realName ? this.item.sysUser.realName : this.item.order.roomUser;
        } else {
            this.userName = this.item.order.roomUser;
        }
        if (this.item.sysUser) {
            this.phone = this.item.sysUser.phone ? this.item.sysUser.phone : this.item.order.phone;
        } else {
            this.phone = this.item.order.phone;
        }
        if (this.item.sysUser) {
            this.IDcard = this.item.sysUser.idenIds ? this.item.sysUser.idenIds : '';
        }
        if (!this.item.rstartTime || !this.item.rendTime) {
            if (this.item.orderType == 1) {
                this.item.rstartTime = this.item.order.startTime.substr(5, 5);
                this.item.rendTime = this.item.order.endTime.substr(5, 5);
            }
        }
        this.time = this.item.rstartTime + '至' + this.item.rendTime;
        this.realName = this.userName;
        this.id = this.item.id;
        this.orderId = this.item.orderId;
        if (this.item.status == 1 && this.back_num == '') {
            this.$store.commit('setHeader', '办理入住');
        } else if (this.item.status == 4) {
            this.$store.commit('setHeader', '扣款押金');
        } else if ((this.item.status == 1 && this.back_num == 9) || (this.item.status == 2 && this.back_num == 9)) {
            this.$store.commit('setHeader', '房费优惠');
        }
        if (this.item.orderType == 2) {
            if (!this.item.roomSn) {
                this.getNum();
            } else {
                this.min_value = (Number(this.item.roomPrice) - Number(this.item.deposit)).toFixed(2);
            }
        } else if (this.item.orderType == 1) {
            if (!this.item.roomSn) {
                this.getNum();
            }
            if ((this.item.priceRecord && this.item.status == 1 && this.back_num == 9) || (this.item.priceRecord && this.item.status == 2 && this.back_num == 9)) {
                this.priceRecord = this.item.priceRecord;
                const priceRecord = JSON.parse(this.priceRecord);
                const arr = [];
                for (var key in priceRecord) {
                    var price = priceRecord[key];
                    arr.push(price);
                }
                let priceRecord_arr = arr;
                // 代码测试
                this.min_value = this.arrayMin(priceRecord_arr);
                if (this.item.order.roomScaleDiscountId) {
                    this.getDiscount();
                } else {
                    this.getMinValue();
                }
            }
        }
        if (!this.item.deposit && this.item.status == 4) {
            this.disabledBtn = true;
            Toast({
                message: '暂无押金',
                duration: 1000
            });
        } else {
            this.max_deposit = this.item.deposit;
        }
    },
    methods: {
        getMinValue() {
            let discount = 1;
            let min_Price = Number(this.min_value);
            let memberDiscount = this.item.order.memberDiscount == null ? 1 : Number(this.item.order.memberDiscount);
            let roomRealAmount = Number(this.item.roomRealAmount);
            let couponAmount = Number(this.item.couponAmount);
            let big = min_Price * discount * memberDiscount;
            let little = (min_Price / roomRealAmount) * couponAmount;
            this.min_value = (big - little).toFixed(2);
        },
        // 计算月份
        getMonths(date1, date2) {
            date1 = date1.split('-');
            date2 = date2.split('-');
            //获取年,月数
            var year1 = parseInt(date1[0]),
                month1 = parseInt(date1[1]),
                year2 = parseInt(date2[0]),
                month2 = parseInt(date2[1]),
                //通过年,月差计算月份差
                months = (year2 - year1) * 12 + (month2 - month1) + 1;
            this.rent_time = months - 1 + '个月';
        },
        // 查找数组中最小值
        arrayMin(arrs) {
            var min = arrs[0];
            for (var i = 1, ilen = arrs.length; i < ilen; i += 1) {
                if (arrs[i] < min) {
                    min = arrs[i];
                }
            }
            return min;
        },
        // 房间号
        getNum() {
            if (this.back_num == 9) {
                return;
            }
            GetBusinessRoomFreeRoomscale(this.item.virtualRoomScaleId).done((res) => {
                this.numList = res.data;
                if ((this.numList.length > 0 && this.item.orderType == '1') || (this.numList.length > 0 && this.item.orderType == '2')) {
                    this.long_num = this.numList[0].roomSn + '    >';
                    this.roomId = this.numList[0].id;
                    this.ammeter = this.numList[0].ammeter;
                }
                if (this.numList.length == 0) {
                    Toast({
                        message: '暂无空闲房间可选择',
                        duration: 900
                    });
                }
            });
        },
        bthShowPicker(obj) {
            if ((obj.type == 'roomPrice' && this.item.order.payment > 5) || (obj.type == 'deposit' && this.item.order.depositPayment > 0)) {
                return;
            } else {
                this.typeInput = obj.type;
                this.month_peymant();
                this.showPicker = true;
            }
        },
        month_peymant() {
            GetSystemDictDataByType({ dictType: 'business_payment' }).done((res) => {
                if (this.typeInput == 'roomPrice') {
                    this.AllColumns = res.data;
                    this.columns = res.data.filter((item) => item.dictValue >= 5).map((item) => item.dictLabel);
                } else {
                    this.AllColumns = res.data;
                    this.columns = res.data.filter((item) => item.dictValue < 1 || item.dictValue >= 5).map((item) => item.dictLabel);
                }
                this.options = res.data.map((item) => {
                    return {
                        label: item.dictLabel,
                        value: item.dictValue
                    };
                });
                let payments = {};
                for (var i = 0; i < this.options.length; i++) {
                    payments[this.options[i].value] = this.options[i].label;
                }
                this.month_payment = payments;
            });
        },
        //表单选择
        onConfirm(val) {
            if (this.typeInput == 'roomPrice') {
                this.pricePaymentNum = this.AllColumns.filter((item) => item.dictValue > 5).find((item) => item.dictLabel == val).dictValue;
                this.roomPricePaymentName = val;
            } else {
                this.depositPaymentNum = this.AllColumns.find((item) => item.dictLabel == val).dictValue;
                this.depositPaymentName = val;
            }
            this.showPicker = false;
        },
        peymant() {
            GetSystemDictDataByType({ dictType: 'business_payment_method' }).done((res) => {
                this.options = res.data.map((item) => {
                    return {
                        label: item.dictLabel,
                        value: item.dictValue
                    };
                });
                let payments = {};
                for (var i = 0; i < this.options.length; i++) {
                    payments[this.options[i].value] = this.options[i].label;
                }
                this.payments = payments;
            });
        },
        // 选择房间号-弹出
        btn_choose() {
            this.show_num = true;
            this.getNum();
        },
        // 选择房间号
        choosed(item) {
            this.long_num = item.roomSn + '    >';
            this.color_num = true;
            this.roomId = item.id;
            this.ammeter = item.ammeter;
            this.show_num = false;
        },
        // 办理入1
        bthDeal() {
            if (this.long_num == '选择房间号') {
                Toast({
                    message: '请选择房间号',
                    duration: 1000,
                    className: 'toasts'
                });
                return false;
            }
            if (!this.userName) {
                Toast({
                    message: '请输入完整姓名',
                    duration: 1000,
                    className: 'toasts'
                });
                return false;
            }
            if (!this.IDcard || !/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(this.IDcard)) {
                Toast({
                    message: '请输入正确的身份证号',
                    duration: 1000,
                    className: 'toasts'
                });
                this.IDcard = '';
                return false;
            }
            // 已验证191,181手机号
            if (!this.phone || !/^1[3456789]\d{9}$/.test(this.phone)) {
                Toast({
                    message: '请输入正确的手机号',
                    duration: 1000,
                    className: 'toasts'
                });
                this.phone = '';
                return false;
            }
            if ((this.item.orderType == 1 && this.item.order.payTimeType == 2 && !this.roomPricePaymentName) || (this.item.orderType == 2 && this.item.order.payTimeType == 2 && !this.roomPricePaymentName)) {
                Toast({
                    message: '请选择到店付支付方式',
                    duration: 1000
                });
                return false;
            }
            let arr = [];
            let obj = {
                roomUser: this.userName,
                idCard: this.IDcard
            };
            arr.push(obj);
            this.arrObj = JSON.stringify(arr);
            if (this.item.orderType == 3 || this.item.orderType == 4) {
                if (this.totalPrice == 0 && this.lifeCost) {
                    let data = {
                        orderRoomId: this.item.id,
                        roomId: this.roomId,
                        phone: this.phone,
                        lifeCost: this.lifeCost,
                        ammeter: this.ammeter,
                        orderId: this.orderId,
                        roomUser: this.arrObj
                    };
                    this.$router.push({
                        path: '/dealLive_long',
                        query: {
                            live_next: data
                        }
                    });
                    localStorage.setItem('live_next', JSON.stringify(data));
                } else {
                    if (this.lifeCost) {
                        let data = {
                            orderRoomId: this.item.id,
                            roomId: this.roomId,
                            phone: this.phone,
                            lifeCost: this.lifeCost,
                            ammeter: this.ammeter,
                            orderId: this.orderId,
                            roomUser: this.arrObj
                        };
                        this.$router.push({
                            path: '/dealLive_long',
                            query: {
                                live_next: data
                            }
                        });
                        localStorage.setItem('live_next', JSON.stringify(data));
                    } else {
                        this.roomAssignComfirm();
                    }
                }
            } else {
                MessageBox.confirm(
                    `姓名：${this.userName}<br />
                    证件号：${this.IDcard}<br />
                    房间号：${this.long_num.substr(0, 6)}<br />
                    手机号：${this.phone}<br />`,
                    '办理入住信息确认'
                ).then((action) => {
                    this.roomAssignComfirm();
                }),
                    (action) => {
                        console.log('取消操作');
                    };
            }
        },
        // 办理入住2
        roomAssignComfirm() {
            let orderRoomId = this.item.id;
            let params = Object.assign({
                orderId: this.orderId,
                roomId: this.roomId,
                phone: this.phone,
                carNo: this.carNum,
                payment: this.pricePaymentNum,
                depositPayment: this.depositPaymentNum,
                roomUser: this.arrObj,
                remark: ''
            });
            PostBusinessOrderroomRoomin(orderRoomId, params).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 1000
                });
                setTimeout('window.history.go(-1)', 2000);
            });
        },
        // 扣款押金1
        bthdeposit() {
            if (!this.redeposit || !/^[0-9]+.?[0-9]*$/.test(this.redeposit)) {
                Toast('请正确输入扣款金额');
                this.redeposit = '';
                return false;
            }
            if (Number(this.max_deposit) < Number(this.redeposit)) {
                Toast(`扣款金额不能超过${this.max_deposit}`);
                this.redeposit = '';
                return false;
            }
            if (!this.reason) {
                Toast('请正确输入扣款原因，并且字数请保持在64个字以内');
                return false;
            }
            MessageBox.confirm(`是否确认扣款<br />`, '提示')
                .then(() => {
                    this.bthdeposit1();
                })
                .catch(() => {});
        },
        // 扣款押金2
        bthdeposit1() {
            let orderRoomId = this.id;
            let data = {
                depositCut: this.redeposit,
                depositCutReason: this.reason
            };
            PostBusinessOrderroomDepositCut(orderRoomId, data).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                this.$router.push('/order');
            });
        },
        // 房费优惠-1
        getDiscount() {
            GetBusinessRoomscaleDiscountInfo(this.item.order.roomScaleDiscountId).done((res) => {
                this.discount = res.data.discount;
                let memberDiscount = this.item.order.memberDiscount ? this.item.order.memberDiscount : 1;
                if (this.item.order.memberDiscount) {
                    this.big = ((Number(this.min_value) * Number(this.discount)) / 10) * Number(memberDiscount);
                } else {
                    this.big = (Number(this.min_value) * Number(this.discount)) / 10;
                }
                if (this.item.couponAmount) {
                    this.little = (Number(this.min_value) / Number(this.item.roomRealAmount)) * Number(this.item.couponAmount);
                } else {
                    this.little = 0;
                }
                this.min_value = (this.big - this.little).toFixed(2);
            });
        },
        // 房费优惠-2
        btnturnback() {
            MessageBox.confirm(`房费优惠将会在客户退房后，立即退还到客户账户<br />`, '提示')
                .then(() => {
                    this.btnturnback1();
                })
                .catch(() => {
                    console.log('取消操作');
                });
        },
        // 房费优惠-3
        btnturnback1() {
            let orderRoomId = this.id;
            let data = {
                orderId: this.orderId,
                refuseLandlord: this.back_roomPrice
            };
            PostBusinessOrderroomRefuseLandlord(orderRoomId, data).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                setTimeout('window.history.go(-2)', 500);
            });
        }
    },
    watch: {
        scaleName(oldName, newName) {
            if (oldName.length > 6) {
                this.scaleName = oldName.substr(0, 5) + '...';
            }
        },
        back_roomPrice(neww, oldd) {
            if (Number(neww) > this.min_value) {
                Toast({
                    message: `优惠金额不能超过${this.min_value}`,
                    duration: 900
                });
                this.back_roomPrice = '';
                neww = '';
            }
            return false;
        }
    }
};
</script>
<style scoped>
/deep/ input::-webkit-input-placeholder {
    color: #b2b2b2;
}
span {
    color: #000;
}
/deep/ .mint-cell-wrapper {
    padding: 0;
    font-size: 0.8rem;
    text-align: left;
    margin-left: 20px;
}
.mint-field {
    margin-bottom: 1px;
}
.popup {
    width: 100%;
    height: 200px;
    overflow-y: scroll;
}
.num_box {
    line-height: 34px;
    border-bottom: 1px solid #f5f5f5;
}
.long {
    display: flex;
    text-align: left;
    line-height: 35px;
    background: #fff;
    padding-left: 36px;
}
.long > div:nth-child(1) {
    border: 1px solid red;
    width: 32%;
}
.long > div:nth-child(2) {
    border: 1px solid greenyellow;
    width: 20%;
}
.long_tips {
    margin-top: 10px;
    font-size: 0.7rem;
    text-align: left;
    padding: 10px 30px;
    line-height: 20px;
    color: #bbb;
}
#bigbox > p {
    text-align: left;
    margin: 10px 34px 6px;
    font-size: 15px;
    color: #000;
    font-weight: bold;
}
.next {
    margin-top: 20px;
    margin-bottom: 15px;
    background-color: #f60;
    width: 100%;
}
.statu_0 {
    background: #d9d9d9;
}
.arriveStore {
    border: 1px solid red;
    display: flex;
    padding: 0 20px;
    background: #fff;
    margin-top: 20px;
    line-height: 40px;
    text-align: left;
    justify-content: space-between;
    box-sizing: border-box;
}
.w30 {
    width: 36%;
    border: 1px solid pink;
}
.c_888 {
    color: #888;
}
.roomTitle {
    color: #999;
    padding: 1rem;
    text-align: left;
    padding: 0 0 10px 20px;
}
</style>



